.form-login h3 {
    color: white;
    margin-bottom: 4rem;
    display: flex;
    width: 100%;
    justify-content: center;
}

.form-login div {
    color: white;
    
}

.form-login div input {
    width: 15rem;
    height: 2rem;
    border-radius: 20px;
    outline: none;
    border-style: none;
    padding-left: 1rem;
}

.form-login button {
    width: 16rem;
    height: 2rem;
    border-radius: 20px;
    outline: none;
    border-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(90deg, #A445B2 0%, #D41872 52%, #FF0066 100%);
    color: white;
    margin-top: 3rem;
    cursor: pointer;
}

.form-login button:hover {
    transform: scale(1.02);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.form-login button:active {
    transform: scale(1);
}

.form-login p {
    color: white;
}