.category {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    row-gap: 3rem;
}

.category .category-selected {
    display: flex;
    column-gap: 3rem;
}

.category .category-selected .option {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 198px;
    height: 64px;
    column-gap: 1rem;
    color: rgba(149, 149, 149, 0.63);
    font-size: 1.3rem;
    cursor: pointer;
}

.category .category-selected .option.active {
    color: white;
    border: 1px solid white;
    border-radius: 40px;
}