.account {
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(5px);
    position: fixed;
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 100;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.account.active {
    display: flex;
}

.close-login {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.close-login button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    outline: none;
    border-style: none;
    position: relative;
    bottom: 20px;
    right: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
    cursor: pointer;
    background: linear-gradient(90deg, #A445B2 0%, #D41872 52%, #FF0066 100%);
}

.form-container {
    height: 80vh;
    width: 35rem;
    display: flex;
    flex-direction: column;
    background: linear-gradient(90deg, rgb(103, 50, 127) 0%, rgba(63, 50, 127, 1) 100%);
    align-items: center;
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.form-container p {
    cursor: pointer;
    color: white;
}

.form-container p:hover {
    color: rgba(255, 255, 255, 0.5);
}

.form-container p:active {
    color: rgba(255, 255, 255, 1);
}