.header {
    display: flex;
    width: 100%;
    height: 85px;
    background-color: #3F327F;
    align-items: center;
}

.header .logo {
    margin-left: 8rem;
    margin-right: 5rem;
}

.header nav {
    display: flex;
    column-gap: 5rem;
    height: 85px;
    width: 100%;
    justify-content: flex-end;
    margin-right: 5rem;
    align-items: center;
}

.header nav a {
    display: flex;
    column-gap: 1rem;
    color: white;
    text-decoration: none;
    font-size: 10px;
}

.header nav div h2 {
    font-size: 1rem;
}

.header .register-login {
    display: flex;
    justify-content: center;
    margin-right: 8rem;
}

.header .register-login button {
    width: 178px;
    height: 44px;
    background: linear-gradient(90deg, #A445B2 0%, #D41872 52%, #FF0066 100%);
    border-radius: 5px;
    outline: none;
    border-style: none;
    color: white;
    cursor: pointer;
    transition: 0.2s;
}

.header .register-login button:hover {
    transform: scale(1.05);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}