.banner {
    width: 100%;
    height: 577px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.banner-slider {
    height: 457px;
    width: 1170px;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, 
                rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.banner-slider .banner-slides {
    display: flex;
    width: 500%;
    height: 457px;
}

.banner-slider .banner-slides input {
    display: none;
}

.manual-slide {
    position: absolute;
    width: 1170px;
    margin-top: 30px;
    display: flex;
    justify-content: center;
}

.manual-slide .manual-btn {
    height: 15px;
    width: 15px;
    border-radius: 10px;
    background: rgba(228, 228, 228, 0.22);
    cursor: pointer;
    transition: 1s;
}

.manual-slide .manual-btn.active {
    width: 40px;
    background-color: rgba(250, 211, 6, 1);
}

.manual-slide .manual-btn:not(:last-child){
    margin-right: 0.5rem;
}

#radio1:checked ~ .first {
    margin-left: 0%;
}
#radio2:checked ~ .first {
    margin-left: -20%;
}
#radio3:checked ~ .first {
    margin-left: -40%;
}
#radio4:checked ~ .first {
    margin-left: -60%;
}

.banner-slide {
    transition: 1s;
}

.banner-slide img {
    width: 1170px;
    height: 457px;
    position: relative;
}

.auto-slide div:not(:last-child) {
    margin-right: 0.5rem;
}

#radio1:checked ~ .auto-slide .auto-btn1 {
    background-color: rgba(250, 211, 6, 1);
    width: 40px;
}
#radio2:checked ~ .auto-slide .auto-btn2 {
    background-color: rgba(250, 211, 6, 1);
    width: 40px;
}
#radio3:checked ~ .auto-slide .auto-btn3 {
    background-color: rgba(250, 211, 6, 1);
    width: 40px;
}
#radio4:checked ~ .auto-slide .auto-btn4 {
    background-color: rgba(250, 211, 6, 1);
    width: 40px;
}